@import "./index";

.home {
    width: 100%;
    min-height: 100%;
    overflow: hidden;

    //Container
    .container {

        //Balance
        .balance {
            padding: 0 2em;
            width: 100%;
            height: 8em;
            background-color: $grey1;
            border-radius: 0.66em;
            display: flex;
            align-items: center;
            justify-content: space-between;
            color: $light0;
            //Text
            .text {
                font-size: 1.5em;
                font-weight: 600;
                span {
                    font-weight: 400; } }

            //Current Balance
            .amount {
                font-size: 2em;
                font-weight: 600; } } }

    //Media Queries
    @media screen and ( max-width: 768px ) {
        display: flex;
        align-items: center;
        justify-content: center; }

    @media screen and ( max-width: 425px ) {
        .container {
            .balance {
                padding: 0 1em;
                font-size: 0.9em; }
            .items {
                margin-top: 1em;
                .item {
                    padding: 1em; } } } } }
